import React, { useRef } from "react";
import { Col, Row } from 'react-bootstrap';
import { useSelector } from "react-redux";

import { Mousewheel, Pagination } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';

const PortDetail = () => {
  const swiperRef = useRef(null); // Swiper 인스턴스 저장용
  const doclist = useSelector((state) => state.doclist);

  const handleNavClick = (index) => {
    if (swiperRef.current) {
      swiperRef.current.slideTo(index);  // index번째 슬라이드로 이동
    }
  };

  return (
    <>
      <div className="port_nav">
        <ul>
          {doclist.map((item, i) => (
            <li key={i} onClick={() => handleNavClick(i)} style={{cursor:'pointer'}}>
              {item.nav}
            </li>
          ))}
        </ul>
      </div>

      <div className="port_wrap">
        <Swiper
          onSwiper={(swiper) => { swiperRef.current = swiper }}
          direction="vertical"
          pagination={{
            clickable: true,
          }}
          nested={true}
          modules={[Pagination, Mousewheel]}
          mousewheel={{
            forceToAxis: true,
            releaseOnEdges: true,
          }}
          className="mySwiper"
        >
          {doclist.map((item, i) => (
            <SwiperSlide key={i}>
               <div className="port_detail_scroll_wrap">
                {/* 슬라이드 내용 */}
                <div
                  className="port_detail"
                  style={{ backgroundColor: item.backColor}}
                >
                  {/* 상세 내용 */}
                  <Row>
                    <Col md={6} lg={7}>
                      <div className="hommepage_wrap">
                        <div className="homepage_inner">
                          <img src={process.env.PUBLIC_URL + item.homeImg} alt={`${item.title} homepage`} />
                        </div>
                      </div>
                    </Col>
                    <Col md={6} lg={5}>
                      <div className="port_text">
                        <h1>{item.title}</h1>
                        <p>{item.mTitle}</p>
                        <h4>{item.date}</h4>
                        <h3>TOOL</h3>
                        <span className="tool">{item.tool}</span>
                        <h3>MOVING TO</h3>
                        <span
                          className="text_btn"
                          onClick={() => window.open(item.homeUrl, '_blank')}
                        >
                          HOME PAGE
                        </span>
                        <span
                          className="text_btn"
                          onClick={() => window.open(item.docUrl, '_blank')}
                        >
                          DOCUMENT
                        </span>
                      </div>
                    </Col>
                  </Row>
                </div>
               </div>
              
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </>
  );
};

export default PortDetail;
